import prodTools from "./prodTools";

let Cookie = {};
//设置cookie
Cookie.setCookie = function (cname,cvalue,exdays) {
  let d = new Date();
  d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
  let expires = "expires=" + d.toUTCString();
  console.info(cname + "=" + cvalue + "; " + expires);
  document.cookie = cname + "=" + cvalue + "; " + expires;
  console.info(document.cookie);
}

//获取cookie
Cookie.getCookie = function (cname) {
  let name = cname + "=";
  let ca = document.cookie.split(';');
  for (var i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) == ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) != -1){
      return c.substring(name.length, c.length);
    }
  }
  return false;
}

//检查cookie是否存在
Cookie.checkCookie = function () {
  let user = this.getCookie("username");
  if (user != "") {
    alert("Welcome again " + user);
  } else {
    return false;
  }
}

//删除cookie
Cookie.delCookie = function(cname) {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cvalue = this.getCookie(cname);
  if (cvalue != null){
    document.cookie = cname + "=" + cvalue + ";expires=" + exp.toGMTString();
  }
}
export default Cookie;




